.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    background-color: #15294B;
    color: #ffffff;
    position: fixed;
    top: 60px;
    left: 0;
    z-index: 999;
    width: 100%;
}
.navbar p{
    font-family: 'Roboto', 'sans-serif';
    font-style: normal;
    font-weight: 400;
}
.navbar-left{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 50px;
}
.navbar-left>p{
    padding: 0 10px 0 40px;
    font-size: 24px;
}
.navbar-left>.MuiSvgIcon-root{
    padding-right: 20px;
    font-size: 30px;
}
.navbar-left>h2{
    padding: 0 40px;
}
.navbar-right{
    margin-right: 30px;
}
.progressbar>p{
    text-align: right;
    font-size: 20px;
    margin: 18px;
}
.navbardesc{
    display: flex;
    align-items: center;
}
.navbardesc>p{
    letter-spacing: 2px;
    margin-right: 30px;
    font-size: 12px;
}
.navbardesc>button{
    padding: 12px 30px;
    border-radius: 50px;
    border: none;
    outline: none;
    background-color: #a5aab9;
    color: #ffffff;
}